/* slider common  */

/* sliick slider common */
.slick-prev,
.slick-next {
  background: #232323;
  color: #fff;
  padding: 4px;
  width: 30px;
  height: 50px;
  z-index: 9;
  border-radius: 3px;
  box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: #fff;
  outline: none;
  background: #fcc101;
}

/* dots */
.slick-dots {
  margin-top: 20px;
}
.slick-dots li button:before {
  font-family: "slick";
  font-size: 14px;
  line-height: 60px;
  position: absolute;
  top: 0;
  left: 0;
}
.slick-dots li.slick-active button:before {
  color: #fcc101;
}
.slick-dots li button:hover:before {
  opacity: 1;
  color: #fcc101;
}

/* slider comm end */
.product_container {
  padding: 15px;
  height: 100%;
}

.product_inn {
  box-shadow: 0 5px 15px rgb(0 0 0 / 10%);
  padding: 15px;
  height: 100%;
  transition: all 0.3s;
}

.product_inn img {
  max-width: 100%;
}

.product_inn:hover {
  transform: translateY(-10px);
}

.img-fluid {
