}

/* title design */

.main_title {
  position: relative;
  margin-bottom: 25px !important;
}
.main_title:before {
  content: "";
  position: absolute;
  display: block;
  width: 120px;
  height: 2px;
  background: #ededed;
  bottom: -8px;
  left: calc(50% - 60px);
}
.main_title:after {
  content: "";
  position: absolute;
  display: block;
  width: 40px;
  height: 4px;
  background: #fcc101;
  bottom: -9px;
  left: calc(50% - 20px);
}

